    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background-color: rgb(191, 214, 206);
}

main {
    flex: 2;
    section {
        max-width: 800px;
        margin: 50px auto;
    }
}

/*section {
    max-width: 800px;
    margin: 50px auto;
}*/
body {
    font-family: 'Comic Sans MS', cursive, sans-serif;
    background-color: #ffe4e1; /* Light pink background */
    color: #5a4d5f; /* Subtle purple-gray for text */
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .container {
    background-color: #fffaf0; /* Creamy background */
    border-radius: 15px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    padding: 20px 30px;
    width: 80%;
    max-width: 400px;
    text-align: center;
  }
  
  h1, p {
    margin-bottom: 20px;
  }
  
  label {
    font-weight: bold;
    color: #a0522d; /* Chocolate color */
  }
  
  input[type="text"] {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 2px solid #ffb6c1; /* Light pink border */
    border-radius: 8px;
    font-size: 14px;
  }
  
  input[type="text"]:focus {
    outline: none;
    border: 2px solid #ff69b4; /* Hot pink border on focus */
  }
  
  .btn-submit {
    background-color: #ff69b4; /* Hot pink */
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 20px;
    cursor: pointer;
    transition: 0.3s ease;
  }
  
  .btn-submit:hover {
    background-color: #ff1493; /* Deeper pink */
    transform: scale(1.05);
  }
  
  section {
    margin-top: 20px;
  }
  
  strong {
    color: #d2691e; /* Chocolate brown */
    display: block;
    margin-bottom: 10px;
  }
  
  div[id^=""] {
    background: #f0f8ff; /* Alice Blue for result sections */
    padding: 10px;
    margin: 5px 0;
    border-radius: 10px;
  }`, "",{"version":3,"sources":["webpack://./src/client/styles/base.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,iBAAiB;IACjB,oCAAoC;AACxC;;AAEA;IACI,OAAO;IACP;QACI,gBAAgB;QAChB,iBAAiB;IACrB;AACJ;;AAEA;;;EAGE;AACF;IACI,iDAAiD;IACjD,yBAAyB,EAAE,0BAA0B;IACrD,cAAc,EAAE,gCAAgC;IAChD,SAAS;IACT,UAAU;IACV,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;EACf;;EAEA;IACE,yBAAyB,EAAE,sBAAsB;IACjD,mBAAmB;IACnB,2CAA2C;IAC3C,kBAAkB;IAClB,UAAU;IACV,gBAAgB;IAChB,kBAAkB;EACpB;;EAEA;IACE,mBAAmB;EACrB;;EAEA;IACE,iBAAiB;IACjB,cAAc,EAAE,oBAAoB;EACtC;;EAEA;IACE,WAAW;IACX,aAAa;IACb,cAAc;IACd,yBAAyB,EAAE,sBAAsB;IACjD,kBAAkB;IAClB,eAAe;EACjB;;EAEA;IACE,aAAa;IACb,yBAAyB,EAAE,6BAA6B;EAC1D;;EAEA;IACE,yBAAyB,EAAE,aAAa;IACxC,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,mBAAmB;IACnB,eAAe;IACf,qBAAqB;EACvB;;EAEA;IACE,yBAAyB,EAAE,gBAAgB;IAC3C,sBAAsB;EACxB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,cAAc,EAAE,oBAAoB;IACpC,cAAc;IACd,mBAAmB;EACrB;;EAEA;IACE,mBAAmB,EAAE,mCAAmC;IACxD,aAAa;IACb,aAAa;IACb,mBAAmB;EACrB","sourcesContent":["body {\n    display: flex;\n    flex-direction: column;\n    min-height: 100vh;\n    background-color: rgb(191, 214, 206);\n}\n\nmain {\n    flex: 2;\n    section {\n        max-width: 800px;\n        margin: 50px auto;\n    }\n}\n\n/*section {\n    max-width: 800px;\n    margin: 50px auto;\n}*/\nbody {\n    font-family: 'Comic Sans MS', cursive, sans-serif;\n    background-color: #ffe4e1; /* Light pink background */\n    color: #5a4d5f; /* Subtle purple-gray for text */\n    margin: 0;\n    padding: 0;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    height: 100vh;\n  }\n  \n  .container {\n    background-color: #fffaf0; /* Creamy background */\n    border-radius: 15px;\n    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);\n    padding: 20px 30px;\n    width: 80%;\n    max-width: 400px;\n    text-align: center;\n  }\n  \n  h1, p {\n    margin-bottom: 20px;\n  }\n  \n  label {\n    font-weight: bold;\n    color: #a0522d; /* Chocolate color */\n